// app/api/blog/route.ts
import { NextResponse } from 'next/server';

export async function GET() {
    const res = await fetch("http://localhost:8000/api/v1/plan/list");
    console.log(123);

    // 1. Get the cookie header from the external API
    const externalCookie = res.headers.get('set-cookie');

    const response = NextResponse.json(await res.json());

    // 2. Forward it to the browser
    if (externalCookie) {
        // You may need to parse this if it contains multiple cookies
        response.headers.set('set-cookie', externalCookie);
    }

    return response;
}
